@import url(https://fonts.googleapis.com/css?family=Poppins&display=swap);
body {
  margin: 0;
  -webkit-font-smoothing: antialiased;
  -moz-osx-font-smoothing: grayscale;
  overflow-x: hidden;
  font-family: Poppins, sans-serif;
  margin-bottom: 50px; }

.container {
  max-width: 1200px;
  margin: auto;
  height: 100%; }

.overlap {
  position: absolute;
  width: 100%;
  height: 100%;
  z-index: 2;
  background: #000;
  opacity: 0.2; }

@media screen and (max-width: 1200px) {
  .container {
    max-width: 90%; } }

.section-title {
  text-align: center;
  margin: 30px 0 20px 0;
  font-size: 28px; }

.header {
  height: 100px;
  background: #fff; }

.header__content {
  display: flex;
  align-items: center;
  justify-content: space-between;
  height: 100%; }

.header__logo {
  text-transform: uppercase;
  font-weight: 900;
  font-size: 32px; }

.header__menu {
  display: flex;
  list-style-type: none; }

.header__menu-item {
  margin: 0 10px;
  position: relative; }
  .header__menu-item a {
    color: rgba(0, 0, 0, 0.7);
    text-decoration: none;
    font-weight: 600;
    width: 100%;
    display: block;
    padding: 0 15px;
    position: relative;
    z-index: 1; }
  .header__menu-item .header__menu-item__arrow {
    border-style: solid;
    border-color: rgba(0, 0, 0, 0.7);
    border-width: 0 2px 2px 0;
    height: 4px;
    width: 4px;
    position: absolute;
    right: 2px;
    top: 8px;
    -webkit-transform: rotate(45deg);
            transform: rotate(45deg);
    z-index: 0;
    border-color: #fff; }

.header__menu-item_selected {
  color: #000 !important; }

.footer {
  position: fixed;
  bottom: 0;
  height: 50px;
  background: #000;
  width: 100%; }

.footer__content {
  display: flex;
  height: 100%; }
  .footer__content a {
    color: #fff; }
  .footer__content .header__menu-item__arrow .header__menu-item__arrow {
    border-style: solid;
    border-color: rgba(0, 0, 0, 0.7);
    border-width: 0 2px 2px 0;
    height: 4px;
    width: 4px;
    position: absolute;
    right: 2px;
    top: 8px;
    -webkit-transform: rotate(45deg);
            transform: rotate(45deg);
    z-index: 0; }
  .footer__content .header__menu-item_selected {
    color: rgba(255, 255, 255, 0.9) !important; }

.menu__header-section {
  max-height: 500px;
  overflow: hidden;
  position: relative; }
  .menu__header-section * ::first-letter {
    text-transform: uppercase; }
  .menu__header-section h1 {
    color: #fff;
    position: absolute;
    top: 50%;
    left: 50%;
    -webkit-transform: translate(-50%, -50%);
            transform: translate(-50%, -50%);
    z-index: 3;
    font-size: 60px;
    margin: 0;
    text-align: center;
    text-transform: capitalize; }

.post-list-container {
  display: flex;
  flex-wrap: wrap;
  justify-content: center;
  max-width: 100%; }
  .post-list-container img {
    max-width: 100%; }
  .post-list-container .post-list__item {
    margin: 10px;
    display: block;
    text-decoration: none;
    max-width: 100%; }
    .post-list-container .post-list__item > a {
      display: block;
      max-width: 100%; }
    .post-list-container .post-list__item a {
      text-decoration: none;
      color: #000; }
    .post-list-container .post-list__item h4 {
      text-align: center;
      max-width: 500px;
      margin: auto;
      margin-top: 10px; }
    .post-list-container .post-list__item .hover-button {
      position: absolute;
      left: 50%;
      top: 50%;
      -webkit-transform: translate(-50%, -50%);
              transform: translate(-50%, -50%);
      padding: 2px 10px;
      border: 1px solid #fff;
      border-radius: 4px;
      opacity: 0;
      color: #fff;
      -webkit-transition: all 0.2s;
      transition: all 0.2s;
      font-size: 16px;
      z-index: 3; }
    .post-list-container .post-list__item .post-list-item__img-container {
      overflow: hidden;
      width: 500px;
      max-height: 500px;
      text-align: center;
      margin: auto;
      position: relative;
      display: flex;
      max-width: 100%; }
      .post-list-container .post-list__item .post-list-item__img-container:before {
        content: '';
        display: block;
        position: absolute;
        left: 0;
        top: 0;
        width: 100%;
        height: 100%;
        opacity: 0;
        -webkit-transition: all 0.2s;
        transition: all 0.2s;
        z-index: 2;
        background-color: #000; }
      .post-list-container .post-list__item .post-list-item__img-container:hover:before {
        opacity: 0.4; }
      .post-list-container .post-list__item .post-list-item__img-container:hover .hover-button {
        opacity: 1; }
      .post-list-container .post-list__item .post-list-item__img-container img {
        max-width: 100%; }

h4::first-letter, h5::first-letter, p::first-letter {
  text-transform: uppercase; }

h4 {
  text-align: right; }

.post-description {
  max-width: 600px;
  margin: auto; }

.comments-container {
  list-style: none; }
  .comments-container li {
    display: block;
    margin-top: 15px;
    border-bottom: 1px solid #8a8a8a; }
    .comments-container li p {
      margin-bottom: 5px;
      font-size: 14px; }
  .comments-container .comment-header {
    display: flex;
    justify-content: space-between;
    align-items: flex-start; }
    .comments-container .comment-header .comment-title {
      margin: 0;
      max-width: 70%;
      font-size: 16px; }
    .comments-container .comment-header .email {
      text-transform: lowercase !important;
      color: #4a4a4a;
      font-size: 14px; }

